import { makeStyles } from '@material-ui/core';
import React from 'react';
import { Link } from 'react-router-dom';
import Header from '../../components/header/Header';

const useStyle = makeStyles({

    container: {
        width: '100%',
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        margin: '10rem 0'
    },
    title: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        maxWidth: '1200px'
    },
    p: {
        fontSize: '2rem',
        color: '#333'
    },
    h1: {
        fontSize: '3rem',
        color: '#444'
    }

});

function Home(){

    const clasess = useStyle();

    return (
        <div>
            <Header />  
            <div className={clasess.container}>
                <div className={clasess.title}>
                    <h1 className={clasess.h1}>Desarrollador Full Stack</h1>
                    <p className={clasess.p}>Hola. Soy Anelvin, Desarrollador Full Stack con 5 años de experiencia en creación de aplicaciones web y móviles.</p>
                </div>
            </div>
        </div>
    );
}

export default Home;